import Vue from 'vue'
import Router from 'vue-router'

import GoogleMap from '@/components/GoogleMap'

import Index from '@/components/index'
import Login from '@/components/login'
import Products from '@/components/products'
import Content from '@/components/content'
import Dashboard from '@/components/Dashboard'
import CustomerCart from '@/components/CustomerCart'
import Checkout from '@/components/CheckoutCard'
import PersonalProfile from '@/components/PersonalProfile'
import OrderManger from '@/components/OrderManger'

import Doublebeddorm from '@/components/doublebeddorm'
import Signaldorm from '@/components/signaldorm'
import Mixdorm from '@/components/mixdorm'
import Tentdorm from '@/components/tentdorm'
import TempProduct from '@/components/TempProduct'

import Dashboardproducts from '@/components/Dashboardproducts'
import Dashboardcoupon from '@/components/Dashboardcoupon'
import Dashboardorderlist from '@/components/Dashboardorderlist'
import Dashboardanalysis from '@/components/Dashboardanalysis'



Vue.use(Router)

export default new Router({
  routes: [

    {
      path:'*',
      redirect:'/index'
    },
    {
      path: '/index',
      name: 'indexpage',
      component: Index,
      children:[
        {
          path: '/index',
          name: 'content',
          component: Content,
        },
        {
          path: '/products',
          name: 'products',
          component: Products,
          children:[
            {
              name:'doublebeddorm',
              path:'/',
              component: Doublebeddorm ,
            },
            {
              name:'signaldorm',
              path:'signaldorm',
              component: Signaldorm ,
            },
            {
              name:'mixdorm',
              path:'mixdorm',
              component: Mixdorm ,
            },
            {
              name:'tentdorm',
              path:'tentdorm',
              component: Tentdorm ,
            },
          ],
        },
        {
          name:'Tempproduct',
          path:'/tempproduct/:id',
          component:TempProduct,
        },
        {
          name:'customercart',
          path:'/customercart',
          component:CustomerCart,
          children:[
            {
              name:'checkout',
              path:'/',
              component:Checkout,
            },
            {
              name:'personalprofile',
              path:'personalprofile',
              component:PersonalProfile,
            },
          ]
        },
        {
          name:'ordermanger',
          path:'/ordermanger',
          component: OrderManger,
        },
        {
          path:'/googlemap',
          name:'googlemap',
          component: GoogleMap,
        },
        {
          name:'login',
          path:'/login',
          component: Login,
        },
      ]
    },

    {
          name:'dashboard',
          path:'/admin',
          component: Dashboard ,
          meta:{requiresAuth:true},
          children:[
            {
              name:'dashboardproducts',
              path:'dashboardproducts',
              component: Dashboardproducts ,
              meta:{requiresAuth:true},
            },
            {
              name:'dashboardcoupon',
              path:'dashboardcoupon',
              component: Dashboardcoupon ,
              meta:{requiresAuth:true},
            },
            {
              name:'dashboardorderlist',
              path:'dashboardorderlist',
              component: Dashboardorderlist ,
              meta:{requiresAuth:true},
            },
            {
              name:'dashboardanalysis',
              path:'dashboardanalysis',
              component: Dashboardanalysis ,
              meta:{requiresAuth:true},
            },
          ]
    }
  ]
})



