<template>
    <div class="d-flex flex-wrap col-md-3 col-12">
        <div class="col-12 pr-md-1 p-0 d-flex justify-content-center align-items-start">
            <form @submit.prevent="formdelivery" class="d-flex flex-md-column flex-md-nowrap flex-row flex-wrap w-100 bg-forth p-1 pl-0">
                
                <div class="form-group d-flex flex-column mb-1 col-md p-md-0 align-items-md-start col-12">
                    <p class="h3 text-primary font-weight-bolder">Hello ,</p>
                    <p class="h6 text-primary font-weight-bold">Search Your Hotel</p>
                    <div class="form-group w-100 mt-2">
                        <label for="exampleInputEmail1" class="text-primary">名子</label>
                        <input v-model="search.HotelName" type="text" name="" id="" class="bg-thrid text-primary form-control SourceLight rounded-0 mt-md-0" placeholder="BR Villa 4000" required>
                    </div>
                    <div class="form-group w-100">
                        <label for="exampleInputEmail1" class="text-primary">入住</label>
                        <input v-model="search.check_in_date" type="date" name="" id="" class="bg-thrid text-primary form-control SourceLight rounded-0 mt-md-0" required>
                    </div>
                    <div class="form-group w-100">
                        <label for="exampleInputEmail1" class="text-primary">退房</label>
                        <input v-model="search.check_out_date" type="date" name="" id="" class="bg-thrid text-primary form-control SourceLight rounded-0 mt-md-0" required>
                    </div>
                </div>

                <div class="form-group col-md p-md-0 m-md-0 col-12">
                    <label for="exampleInputEmail1" class="text-primary">客人數</label>
                    <input v-model="search.guestsnum" class="bg-thrid text-primary form-control rounded-0 SourceLight" type="number" name="" id="">
                </div>
                
                <button type="submit" class="btn btn-outline-primary col-12 align-self-end mt-3 font-weight-bold ">送出</button>
            </form>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            search:{
                HotelName:'',
                check_in_date:'yyyy-MM-dd',
                check_out_date:'yyyy-MM-dd',
                guestsnum:1,
            },
        }
    },
    methods:{
        formdelivery(){
            this.$bus.$emit('Search:push',this.search);
        },
    }
}
</script>filter.
<style lang="scss" scoped>

</style>



