<template>
    <div class="d-sm-flex flex-sm-row flex-sm-wrap justify-content-md-start justify-content-center">
        <ProductsTopListMD class="product-ui"/>
        <ProductsSearchMD class="product-ui"/>
        <ProductsSearchXS class="product-ui-xs"/>
        <ProductsTopListXS class="product-ui-xs"/>
        <router-view id="card-group" class="d-sm-flex flex-sm-wrap col-xl-9 col-md-9 mt-md-0 mt-sm-6 col-12 p-sm-0 p-1" data-aos="fade-up" data-aos-duration="1000"></router-view>
    </div>
</template>

<script>
import ProductsSearchXS from '@/components/ProductsSearch-xs'
import ProductsSearchMD from '@/components/ProductsSearch-md'
import ProductsTopListMD from '@/components/ProductsTopList-md'
import ProductsTopListXS from '@/components/ProductsTopList-xs'

export default {
    data(){
        return{
            check_in_date:'0000-00-00',
            check_out_date:'0000-00-00',
            guestsnum:1,
        };
    },
    components:{
        ProductsTopListMD,
        ProductsSearchXS,
        ProductsSearchMD,
        ProductsTopListXS
    },
    methods: {
        filterdatapush(){
            this.$bus.$emit('filterdatapush',this.checkinday,this.checkoutday,this.guestsnum);
        },
    },
}
</script>


<style lang="scss" scoped>
.product-ui{
    display: flex !important;
}
.product-ui-xs{
    display: none !important;
}
@media only screen and (max-width:768px){
    .product-ui{
        display: none !important;
    }
    .product-ui-xs{
        display: flex !important;
    }
}
</style>



